.publication-page {
    padding: 100px 20px 0px;
    max-width: 1000px;
    font-family: "Caladea", serif;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.year-section {
    margin-bottom: 40px;
    font-weight: 400;
    font-style: normal;
}

.year-section h2 {
    margin-bottom: 20px;
    padding-bottom: 5px;
}

.publication-cards {
    display: flex;
    width: 1000px;
    align-items: center;
    flex-direction: column;
    gap: 15px;
}

.publication-card {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    width: 95%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.publication-card:hover {
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.publication-card h3 {
    margin: 0;
    font-size: 18px;
    color: #333;
}

.publication-card p {
    margin-top: 10px;
    color: #555;
    font-size: 14px;
}

.publication-card a {
    color: #5582d6;
    font-size: 14px;
}

.publication-card span {
    color: #555;
    font-size: 14px;
}

.links {
    margin-top: 5px;
    display: flex;
    gap: 10px;
}

.links a {
    text-decoration: none;
    color: #5582d6;
    font-weight: bold;
    transition: color 0.2s ease;
}

.links a:hover {
    color: #0056b3;
}
